@import '../../base';

.placePage{
    height: 100%;

    .content{
        min-height: calc(100% - 8.3em);
        margin: 0 15%;
        display: grid;
        place-content: center;
        
        figure{
            place-self: center;
            margin: 1em;
            width: 80%;
            box-shadow: 0 0 0.2em 0.1em black;
            img{
                place-self: center;
                display: block;
                width: 100%;
                height: 500px;
            }
        }

        .info{
            border: 1px solid black;
            font-size: 18px;

            div:nth-of-type(1){
                display: flex;
                justify-content: space-around;
                border-bottom: 1px solid black;
                background-color: $firstColor;
                color: $fifthColor;
                width: 100%;

                h2{
                    flex-basis: 50%;
                    padding: 0.8em;
                }

                p{
                    flex-basis: 25%;
                    padding: 1em;
                    line-height: 200%;
                    text-align: center;
                }

                a{
                    flex-basis: 25%;
                    vertical-align: middle;
                    line-height: 200%;
                    text-align: center;
                    font-weight: bold;
                    color: #ecf0f1;
                    padding: 1em;
                }

                a:hover{
                    background-color: $thirdColor;
                }
            }

            div:nth-of-type(2){
                display: flex;
                justify-content: space-around;
                border-bottom: 1px solid black;
                padding: 1em;
                background-color: $secondColor;
                color: $sixthColor;
                width: 100%;
            }
            div:nth-of-type(3){
                line-height: 150%;
                text-align: center;
                padding: 2em 5em;
                width: 100%;
            }
        }
    }

}

@media (max-width: 768px){
    .placePage{
 
        .content{
            margin: 1% ;

            figure{
                width: 100%;
            }
        }
    }
}