@import '../../../base';

nav{
    ul{
        display: flex;
        background-color: $secondColor;

        li{
            padding: 1.5em;

            a{
                padding: 2em;
                color: $thirdColor;
            }
            :hover{
                color: $fourthColor;
            }
        }
    }
}

@media (max-width:768px){
    nav{
        ul{
            display: flex;
            background-color: $secondColor;
    
            li{
                padding: 1.5em;
    
                a{
                    padding: 1em;
                    color: $thirdColor;
                }
            }
        }
    }
}